import React from "react"
import { connect } from "react-redux";

import { login, logout, signup } from '../store/user.action'


export class _UserLogin extends React.Component {
    state = {
        user: this.props.loggedinUser,
        input: {
            username: '',
            password: '',
            fullname: '',
        },
        isSignupShown: false
    }

    usernameInputRef = React.createRef()

    componentDidMount() {
        this.usernameInputRef.current.focus()
    }




    handleChange = ({ target }) => {
        const field = target.name
        const value = target.value
        this.setState(prevState => ({ input: { ...prevState.input, [field]: value } }))
    }

    onSubmit = (ev) => {
        ev.preventDefault()
        if (!this.state.isSignupShown) this.onLogin()
        else this.onSignup()

    }


    // TODO: add user msg
    onLogin = async () => {
        const { username, password } = this.state.input
        const credentials = { username, password }
        try {
            await this.props.login(credentials)
            this.onGoToBack()
        } catch (err) {
            console.log('Wrong username or password');
        }
    }

    // TODO: add logout mgs
    onLogout = async () => {
        try {
            await this.props.logout()
            this.setState({ user: null });
        } catch (err) {
            console.log('Cannot logout', err);
        }
    }

    onSignup = async () => {
        try {
            const { input } = this.state
            await this.props.signup(input)
            this.onGoToBack()
        } catch (err) {
            console.log('Cannot signup', err);
        }

    }

    onToggleSignupShown = () => {
        this.setState({ isSignupShown: !this.state.isSignupShown })
    }

    onGoToBack = () => {
        this.props.history.push('/')
    }

    render() {

        const { user, isSignupShown } = this.state
        const { username, password, fullname } = this.state.input

        return (
            <section className="user-login">
                {!isSignupShown &&
                    <React.Fragment>
                        <h2>Login</h2>
                        {user && `Hello ${user.fullname}`}
                        <form onSubmit={this.onSubmit}>
                            <label> Username:
                                <input ref={this.usernameInputRef} type="text" name="username" value={username}
                                    placeholder="Enter username here" onChange={this.handleChange} />
                            </label>
                            <label>Password:
                                <input type="password" name="password" value={password}
                                    placeholder="Enter password here" onChange={this.handleChange} />
                            </label>
                            <button >Login</button>
                        </form>
                        <button onClick={this.onLogout}>Logout</button>
                        <button onClick={this.onToggleSignupShown}>Signup</button>
                    </React.Fragment>}
                {isSignupShown &&
                    <React.Fragment>
                        <h2>Signup</h2>
                        {user && `Hello ${user.fullname}`}
                        <form onSubmit={this.onSubmit}>
                            <label> Full name:
                                <input ref={this.usernameInputRef} type="text" name="fullname" value={fullname}
                                    placeholder="Enter your full name here" onChange={this.handleChange} />
                            </label>
                            <label> Username:
                                <input ref={this.usernameInputRef} type="text" name="username" value={username}
                                    placeholder="Enter username here" onChange={this.handleChange} />
                            </label>
                            <label>Password:
                                <input type="password" name="password" value={password}
                                    placeholder="Enter password here" onChange={this.handleChange} />
                            </label>
                            <button >Signup</button>
                        </form>
                        <span>Aleady user?</span>
                        <button onClick={this.onToggleSignupShown}>Login</button>
                    </React.Fragment>}


            </section>
        )
    }
}


function mapStateToProps({ userModule }) {
    return {
        loggedinUser: userModule.loggedinUser,
        userModule
    }
}

const mapDispatchToProps = {
    login,
    logout,
    signup
}

export const UserLogin = connect(mapStateToProps, mapDispatchToProps)(_UserLogin)