import React from "react";
import { connect } from "react-redux";
import { NavLink } from "react-router-dom";




class _AppHeader extends React.Component {


    render() {

        const { user } = this.props

        return (
            <header className="app-header full main-layout">
                <section>
                    <h1>MisterToy</h1>
                    <nav className="main-nav">
                        <ul>
                            <NavLink activeClassName="my-active" to="/dashboard">Dashboard</NavLink>
                            <NavLink activeClassName="my-active" to="/about">About</NavLink>
                            <NavLink activeClassName="my-active" exact to="/login">Login</NavLink>
                            {user?.isAdmin && <NavLink activeClassName="my-active" to="/user">Users</NavLink>}
                            <NavLink activeClassName="my-active" exact to="/">Home</NavLink>
                        </ul>
                    </nav>
                </section>
            </header >
        )
    }
}


function mapStateToProps({ userModule }) {
    return {
        user: userModule.loggedinUser
    }
}

const mapDispatchToProps = {
};


export const AppHeader = connect(mapStateToProps, mapDispatchToProps)(_AppHeader)




