import { httpService } from './http.service'


export const toyService = {
    query,
    remove,
    getById,
    save,
    formatPrice,
    formatDate
}

function query(filterBy, sortBy) {
    return httpService.get(`toy`, { filterBy, sortBy })
}


async function getById(toyId) {
    const user = await httpService.get(`toy/${toyId}`)
    return user;
}


async function remove(toyId) {
    return await httpService.delete(`toy/${toyId}`)
}


async function save(toy) {

    if (toy._id) {
        toy = await httpService.put(`toy/${toy._id}`, toy)
        return toy
    } else {
        toy = await  httpService.post(`toy/`, toy)
        return toy
    }
}


function formatPrice(price, currencyCode = 'ILS') {
    const priceFormatted = new Intl.NumberFormat(
        currencyCode, { style: 'currency', currency: currencyCode }).format(price)
    return priceFormatted
}

function formatDate(createdAt) {
    return new Date(createdAt).toLocaleDateString('he-IL') + ' ' +
        new Date(createdAt).toLocaleTimeString('he-IL')
}


