import { toyService } from "../services/toy.serivce";
import Select from "react-select";

import React from "react";
import { connect } from "react-redux";

import { ImgUpload } from "../cmps/ImgUpload";
import { updateToy, addToy } from '../store/toy.action'

class _ToyEdit extends React.Component {
    state = {
        toy: {
            name: '',
            price: 0,
            inStock: '',
            labels: '',
        },
        isToyLoaded: false
    }

    componentDidMount() {
        this.loadToy()
    }


    loadToy = async () => {
        const { toyId } = this.props.match.params
        if (!toyId) return
        const toy = await toyService.getById(toyId)
        this.setState({ toy })
        this.setState({ isToyLoaded: true })
    }

    onSetImg = (imgUrl) => {
        this.setState({ toy: { ...this.state.toy, src: imgUrl } })
    }

    onSaveToy = async () => {
        let toyToSave = this.state.toy
        // if (imgUrl) toyToSave.src = imgUrl
        if (toyToSave._id) {
            await this.props.updateToy(toyToSave)
            this.onGoBack()
        } else {
            await this.props.addToy(toyToSave)
            this.onGoBack()

        }
    }


    handleChange = ({ target }) => {
        const field = target.name
        const value = target.type === 'number' ? +target.value : target.value
        console.log(value);
        this.setState((prevState) => ({ toy: { ...prevState.toy, [field]: value } }))
    }

    handleSelect = (labels) => {
        this.setState((prevState) => ({ toy: { ...prevState.toy, labels } }))
    }

    toggleChecked = () => {
        this.setState((prevState) => ({ toy: { ...prevState.toy, inStock: !this.state.toy.inStock } }))
    }


    onGoBack = () => {
        this.props.history.push('/')
    }

    render() {

        const { toy, isToyLoaded } = this.state
        const { name, price, labels, inStock, src } = toy


        const options = [
            { value: 'onWheels', label: 'On wheels' },
            { value: 'boxGame', label: 'Box game' },
            { value: 'art', label: 'Art' },
            { value: 'baby', label: 'Baby' },
            { value: 'doll', label: 'Doll' },
            { value: 'puzzle', label: 'Puzzle' },
            { value: 'outdoor', label: 'Outdoor' }
        ]


        return (
            <section className="toy-edit">

                <h2>{isToyLoaded ? 'Update' : 'Add'} Toy</h2>
                <form >
                    <label >Name
                        <input type="text" name="name" value={name} onChange={this.handleChange} />
                    </label>
                    <label>
                        Price
                        <input type="number" name="price" value={price} onChange={this.handleChange} />
                    </label>
                    <label >
                        In Stock:
                        <input type="checkbox" checked={inStock} onChange={this.toggleChecked} />
                    </label>
                    <label >
                        By Labels:
                        <Select className="react-select" isMulti options={options} value={labels} onChange={this.handleSelect} />
                    </label>
                </form>

                {src && <img src={src} alt="" />}

                <ImgUpload onSaveToy={this.onSaveToy} onSetImg={this.onSetImg} />

                <button className="btn-primary" onClick={this.onSaveToy}>{isToyLoaded ? 'Update' : 'Add'}</button>
                <button className="btn-primary" onClick={this.onGoBack}>Back</button>
            </section>
        )
    }
}

function mapStateToProps({ toyModule }) {
    return {
        toys: toyModule.toys
    }
}

const mapDispatchToProps = {
    updateToy,
    addToy
}

export const ToyEdit = connect(mapStateToProps, mapDispatchToProps)(_ToyEdit)