import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import { toyService } from "../services/toy.serivce";
import { ToyReview } from '../cmps/ToyReview'

import { updateToy } from '../store/toy.action'

class _ToyDetails extends React.Component {
    state = {
        toy: null

    }

    componentDidMount() {
        this.loadToy()
    }

    loadToy = () => {
        const { toyId } = this.props.match.params
        toyService.getById(toyId)
            .then(toy => {
                this.setState({ toy })
            })
    }


    onGoBack = () => {
        this.props.history.push('/')
    }

    render() {

        const { toy } = this.state
        const { user } = this.props

        if (!toy) return <React.Fragment />

        const { _id, name, price, labels, createdAt, inStock, src } = toy

        return (
            <section className="toy-details">
                <article className="details">

                    <h2>{name}</h2>
                    {src && <div className="img-container"> <img src={src} alt="" /> </div>}
                    <div><span>Price: </span> {toyService.formatPrice(price)}</div>

                    <div> <span>Created At:</span>  {toyService.formatDate(createdAt)} </div>
                    <div><span>Availability: </span> {inStock ? 'In Stock' : 'Out of Stock'}</div>
                    <span>Labels </span>
                    <div className="labels-container">{labels.map((label, idx) => {
                        return <div key={idx}>{label.label}</div>
                    })}</div>

                    <div className="btns-container">
                        <button className="btn-fa fa-solid arrow-left" onClick={this.onGoBack}></button>
                        {user?.isAdmin && <button className="btn-fa fa edit-filled"> <Link to={`/edit/${_id}`}></Link> </button>}
                    </div>
                </article>
                <ToyReview />
            </section>
        )

    }
}

function mapStateToProps({ userModule }) {
    return {
        user: userModule.loggedinUser
    }
}

const mapDispatchToProps = {
    updateToy,

}

export const ToyDetails = connect(mapStateToProps, mapDispatchToProps)(_ToyDetails)