import { Link } from "react-router-dom";

import { toyService } from "../services/toy.serivce";

export function User(props) {

    const { onRemoveUser, loggedinUser } = props
    const { username, fullname, createdAt, isAdmin, _id } = props.user

    return (
        <tr>
            <td> {username}</td>
            <td>{fullname}</td>
            <td>{toyService.formatDate(createdAt)}</td>
            <td>{isAdmin ? 'Admin' : 'Normal User'}</td>
            <td> {(_id !== loggedinUser._id) && <button className="btn-primary" onClick={() => onRemoveUser(_id)}>Delete</button>}</td>
            <td> <button className="btn-primary"><Link to={`/user/${_id}`}>Edit</Link></button> </td>
        </tr >
    )

}