import React, { useState } from "react";
import { connect } from "react-redux";
import { Image } from "cloudinary-react";
import axios from "axios";

import { Loader } from './Loader'

import { addToy, updateToy } from '../store/toy.action'

export function _ImgUpload({ onSaveToy, onSetImg }) {

    const [IsImgLoading, setIsImageLoading] = useState(false)
    const [imgUrl, setImageUrl] = useState('')


    const uploadImg = async (ev) => {
        setIsImageLoading(true)
        const CLOUD_NAME = 'diu2bzkko'
        const UPLOAD_URL = `https://api.cloudinary.com/v1_1/${CLOUD_NAME}/image/upload`

        const formData = new FormData();
        formData.append('file', ev.target.files[0])
        formData.append('upload_preset', 'xeftevjk');
        try {
            const { data } = await axios.post(UPLOAD_URL, formData)
            const imgUrl = data.url
            setImageUrl(imgUrl)
            setIsImageLoading(false)
            onSetImg(imgUrl)
        } catch (err) {
            console.log('Cannot upload image', err);
        }
    }

    return (


        <div className="img-upload">
            <input type="file" onChange={(ev) => uploadImg(ev)} />
            {IsImgLoading && <Loader />}

            {/* {imgUrl && <Image style={{ width: 200 }} cloudName="diu2bzkko" publicId={imgUrl} />} */}

        </div>
    )


}


function mapStateToProps({ toyModule }) {
    return {
        toys: toyModule.toys
    }
}

const mapDispatchToProps = {
    updateToy,
    addToy
}

export const ImgUpload = connect(mapStateToProps, mapDispatchToProps)(_ImgUpload)