import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";

// ACTIONS
import { loadToys, removeToy, setSort } from '../store/toy.action'

// CMPS
import { ToyList } from '../cmps/ToyList'
import { ToyFilter } from '../cmps/ToyFilter'


class _ToyApp extends React.Component {
    state = {
        user: this.props.loggedinUser
    }

    componentDidMount = () => {
        this.props.loadToys()
    }



    onRemoveToy = (toyId) => {
        try {
            this.props.removeToy(toyId)
        } catch (err) {
            console.log('Admin Operation!');
        }
    }

    onSetSort = async (sortBy) => {
        await this.props.setSort(sortBy)
        this.props.loadToys()
    }

    render() {

        const { toys } = this.props
        if (!toys) return <React.Fragment />

        const { user } = this.state

        return (
            <section className="toy-app">
                <div className="user-greet">Hello {(user) ? user.fullname?.split(' ')[0] : 'guest'} </div>
                <ToyFilter />
                <h3> Sort Toys By:</h3>
                <div className="btns-container">
                    <div className="sort-by">
                        <button className="btn-primary" onClick={() => this.onSetSort('name')}>Name</button>
                        <button className="btn-primary" onClick={() => this.onSetSort('price')}>Price</button>
                    </div>
                    {user?.isAdmin && <button className="btn-primary"><Link to={'/edit'}> Add Toy</Link></button>}
                </div>
                <ToyList toys={toys} user={user} onRemoveToy={this.onRemoveToy} />
            </section>
        )
    }
}



function mapStateToProps(state) {
    return {
        toys: state.toyModule.toys,
        loggedinUser: state.userModule.loggedinUser
    }
}

const mapDispatchToProps = {
    loadToys,
    removeToy,
    setSort
}

export const ToyApp = connect(mapStateToProps, mapDispatchToProps)(_ToyApp)