import React from 'react'
import { Route, Switch } from 'react-router-dom'

import { AppHeader } from './cmps/AppHeader'
import { About } from './pages/About'
import { ToyApp } from './pages/ToyApp'
import { ToyEdit } from './pages/ToyEdit'
import { ToyDetails } from './pages/ToyDetails'
import { ToyDashboard } from './pages/ToyDashboard'
import { UserLogin } from './pages/UserLogin'
import { ToyUser } from './pages/ToyUser'
import { UserEdit } from './pages/UserEdit'
import { ReviewEdit } from './pages/ReviewEdit'

export class RootCmp extends React.Component {

    render() {
        return (
            <section className="app main-layout">
                <AppHeader />
                <main>
                    <Switch>
                        <Route component={ToyEdit} path="/edit/:toyId?"></Route>
                        <Route component={ToyDashboard} path="/dashboard"></Route>
                        <Route component={UserLogin} path="/login"></Route>
                        <Route component={ReviewEdit} path="/review/edit"></Route>
                        <Route component={About} path="/about"></Route>
                        <Route component={UserEdit} path="/user/:userId" />
                        <Route component={ToyUser} path="/user" />
                        <Route component={ToyDetails} path="/:toyId"></Route>
                        <Route component={ToyApp} path="/"></Route>
                    </Switch>
                </main>
            </section>
        )
    }
}


