
import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";

// ACTIONS
import { loadToys } from '../store/toy.action'

// CMPS

import {ToyMap} from '../cmps/ToyMap'


class _About extends React.Component {
    state = {

    }

    componentDidMount() {
        this.props.loadToys()
    }



    render() {

        const { toys } = this.props
        if (!toys) return <React.Fragment />

        return (
            <section className="toy-dashboard">
                <h2>About</h2>
                <ToyMap toys={toys} />
            </section>
        )
    }
}



function mapStateToProps({ toyModule }) {
    return {
        toys: toyModule.toys
    }
}

const mapDispatchToProps = {
    loadToys,
}

export const About = connect(mapStateToProps, mapDispatchToProps)(_About)