
import React from "react";
import { connect } from "react-redux";

// ACTIONS
import { loadToys } from '../store/toy.action'

// CMPS

import { ToysPerYear } from '../cmps/charts/ToysPerYear'
import { PricePerLabel } from "../cmps/charts/PricePerLabel";


class _ToyDashboard extends React.Component {
    state = {

    }

    componentDidMount() {
        this.props.loadToys()
    }



    render() {

        const { toys } = this.props
        if (!toys) return <React.Fragment />

        return (
            <section className="toy-dashboard">
                <h2>Statistics</h2>
                <div className="charts-container">
                    <ToysPerYear toys={toys} />
                    <PricePerLabel toys={toys} />
                </div>
            </section>
        )
    }
}



function mapStateToProps({ toyModule }) {
    return {
        toys: toyModule.toys
    }
}

const mapDispatchToProps = {
    loadToys,
}

export const ToyDashboard = connect(mapStateToProps, mapDispatchToProps)(_ToyDashboard)