import { toyService } from "../services/toy.serivce";

import React from "react";
import { connect } from "react-redux";

import { userService } from "../services/user.service";
import { updateUser } from '../store/user.action'

class _UserEdit extends React.Component {
    state = {
        user: {
            username: '',
            fullname: '',
            isAdmin: false
        }
    }

    async componentDidMount() {
        const { userId } = this.props.match.params
        const user = await userService.getUserById(userId)
        this.setState({ user })

    }

    onUpdateUser = async () => {
        let userToSave = this.state.user
        await this.props.updateUser(userToSave)
        this.onGoBack()
    }


    handleChange = ({ target }) => {
        const field = target.name
        const value = target.value
        this.setState((prevState) => ({ user: { ...prevState.user, [field]: value } }))
    }

    toggleChecked = () => {
        this.setState((prevState) => ({ user: { ...prevState.user, isAdmin: !this.state.user.isAdmin } }))
    }


    onGoBack = () => {
        this.props.history.push('/user')
    }

    render() {

        const { fullname, username, isAdmin } = this.state.user

        const options = [
            { value: 'onWheels', label: 'On wheels' },
            { value: 'boxGame', label: 'Box game' },
            { value: 'art', label: 'Art' },
            { value: 'baby', label: 'Baby' },
            { value: 'doll', label: 'Doll' },
            { value: 'puzzle', label: 'Puzzle' },
            { value: 'outdoor', label: 'Outdoor' }
        ]


        return (
            <section className="toy-edit">

                <h2>Edit User</h2>
                <form >
                    <label >User Name
                        <input type="text" name="username" value={username} onChange={this.handleChange} />
                    </label>
                    <label >Full Name
                        <input type="text" name="fullname" value={fullname} onChange={this.handleChange} />
                    </label>
                    <label >
                        Is Admin
                        <input type="checkbox" checked={isAdmin} onChange={this.toggleChecked} />
                    </label>

                </form>
                <button className="btn-primary" onClick={this.onUpdateUser}>Save</button>
                <button className="btn-primary" onClick={this.onGoBack}>Back</button>
            </section>
        )

    }
}

function mapStateToProps(state) {
    return {

    }
}

const mapDispatchToProps = {
    updateUser
}

export const UserEdit = connect(mapStateToProps, mapDispatchToProps)(_UserEdit)