import React from "react";
import Select from "react-select";
import { connect } from "react-redux";

import { setFilter, loadToys } from '../store/toy.action'

class _ToyFilter extends React.Component {
    state = {
        filterBy: {
            toyName: '',
            inStock: 'all',
            labels: ''
        },
    }

    handleChange = ({ target }) => {
        const field = target.name
        const value = target.type === 'number' ? +target.value : target.value
        this.setState((prevState) => ({ filterBy: { ...prevState.filterBy, [field]: value } }), () => {
            this.props.setFilter(this.state.filterBy)
            this.props.loadToys()

        })
    }

    handleSelect = (labels) => {
        this.setState((prevState) => ({ filterBy: { ...prevState.filterBy, labels } }), () => {
            this.props.setFilter(this.state.filterBy)
            this.props.loadToys()
        })
    }

    onSubmit = (ev) => {
        ev.preventDefault()
        this.clearForm()
    }
    
    // onSubmit = async (ev) => {
    //     ev.preventDefault()
    //     await this.props.setFilter(this.state.filterBy)
    //     this.props.loadToys()
    //     this.clearForm()
    // }

    clearForm = () => {
        this.setState({ filterBy: { toyName: '', inStock: 'all', labels: '' } }, () => {
            this.props.setFilter(this.state.filterBy)
            this.props.loadToys()
        })
    }

    render() {

        const { filterBy } = this.state
        const { toyName, inStock, labels } = filterBy


        const options = [
            { value: 'onWheels', label: 'On wheels' },
            { value: 'boxGame', label: 'Box game' },
            { value: 'art', label: 'Art' },
            { value: 'baby', label: 'Baby' },
            { value: 'doll', label: 'Doll' },
            { value: 'puzzle', label: 'Puzzle' },
            { value: 'outdoor', label: 'Outdoor' }
        ]

        return (
            <div className="toy-filter">
                <h3>Filter Toys: </h3>
                <form onSubmit={(ev) => { this.onSubmit(ev) }} >

                    <label >
                        <div> By Name: </div>
                        <input type="text" name="toyName" placeholder="Enter toy name"
                            value={toyName} onChange={this.handleChange} />
                    </label>
                    <label >
                        <div>  By Labels:</div>
                        <Select className="react-select" isMulti options={options} value={labels} onChange={this.handleSelect} />
                    </label>
                    <label >
                        <div>By Availability:</div>
                        <select className="select" name="inStock" value={inStock} onChange={this.handleChange}>
                            <option value="all">All</option>
                            <option value={true}>In Stock</option>
                            <option value={false}>Out of Stock</option>
                        </select>
                    </label>
                    <button className="btn-primary">Reset Filter</button>
                </form>
                
            </div>
        )
    }
}

function mapStateToProps({ toyModule }) {
    return {
        filterBy: toyModule.filterBy
    }
}

const mapDispatchToProps = {
    setFilter,
    loadToys,

}

export const ToyFilter = connect(mapStateToProps, mapDispatchToProps)(_ToyFilter)