import React from "react";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Doughnut } from 'react-chartjs-2';
import { utilService } from "../../services/util.service";



ChartJS.register(ArcElement, Tooltip, Legend);

export function PricePerLabel({ toys }) {

    let labels = []

    toys.forEach(toy => {
        return toy.labels.forEach(label => {
            if (labels.indexOf(Object.values(label)) === -1) return labels.push(Object.values(label))
        })
    })

    console.log(labels);

    const data = {
        labels: labels,
        datasets: [
            {
                label: '# of Votes',
                data: labels.map(() => 1),
                backgroundColor: labels.map(() => utilService.getRandomColor()),
                borderWidth: 1,
            },
        ],
    };

    return (
        <section className="price-per-label">
            <h3>Average price per toy label</h3>
            <Doughnut data={data} />
        </section>
    )

}

