import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';

const AnyReactComponent = ({ text }) => <div>HEllopopfjp</div>;

export class ToyMap extends Component {
    state = {
        center: {
            lat: 32.109333,
            lng: 34.855499
        },
        zoom: 11
    }

    render() {

        // const { mapOpts } = this.state
        const { center, zoom } = this.state
        return (
            // Important! Always set the container height explicitly
            <div style={{ height: '100vh', width: '100%' }}>
                <GoogleMapReact
                    bootstrapURLKeys={{ key: 'AIzaSyBa1omKmZYI4Bz8r_Kn-FgGdHdMWKCZymM' }}
                    defaultCenter={center}
                    defaultZoom={zoom}
                >
                    <AnyReactComponent
                        lat={32.109333}
                        lng={34.855499}
                        text="My Marker"
                    />
                </GoogleMapReact>
            </div>
        );
    }
}



