import { toyService } from "../services/toy.serivce";

export function loadToys() {
    return async (dispatch, getState) => {

        const { filterBy, sortBy } = getState().toyModule
        try {
            const toys = await toyService.query(filterBy, sortBy)
            dispatch({ type: 'SET_TOYS', toys })
            return Promise.resolve(toys)
        } catch (error) {
            console.log('Cannot get Toys', error);
        }
    }
}

export function removeToy(toyId) {
    return async (dispatch) => {
        try {
            await toyService.remove(toyId)
            dispatch({ type: 'REMOVE_TOY', toyId })
            return Promise.resolve()
        } catch (err) {
            throw err
        }
    }
}

export function updateToy(toyToUpdate) {
    return async (dispatch) => {
        try {
            const savedToy = await toyService.save(toyToUpdate)
            dispatch({ type: 'UPDATE_TOY', toy: savedToy })
            return Promise.resolve(savedToy)
        } catch (err) {
            console.log('Cannot Update', toyToUpdate)
        }
    }
}

export function addToy(toyToSave) {
    return async (dispatch) => {
        try {
            const savedToy = await toyService.save(toyToSave)
            dispatch({ type: 'ADD_TOY', toy: savedToy })
            return Promise.resolve(savedToy)
        } catch (err) {
            console.log('Cannot Add', toyToSave)
        }
    }
}

export function setFilter(filterBy) {
    return async (dispatch) => {
        dispatch({ type: 'SET_FILTER', filterBy })
        return Promise.resolve()
    }
}

export function setSort(sortBy) {
    return async (dispatch) => {
        dispatch({ type: 'SET_SORT', sortBy })
        return Promise.resolve()
    }
}


