import { Link } from "react-router-dom"
import { toyService } from "../services/toy.serivce"

export function ToyPreview(props) {

    const { toy, onRemoveToy, user } = props
    const { name, price, lables, createdAt, inStock, _id, src } = toy

    return (

        <section className="toy-preview">
            <h4>{name}</h4>
            {src && <img src={src} alt="" />}
            <div className="lower-section">
                <div> {toyService.formatPrice(price)}</div>
                {user?.isAdmin && <button className="fa trash btn-fa" onClick={() => onRemoveToy(_id)}></button>}
                {user?.isAdmin && <Link className="fa edit-filled btn-fa" to={`/edit/${_id}`}></Link>}
                <Link className="btn-primary" to={`/${_id}`}>More Details</Link>
            </div>
        </section >
    )
}