

const initialState = {
    toys: [],
    filterBy: null,
    sortBy: null
}

export function toyReducer(state = initialState, action) {

    let newState = state;

    switch (action.type) {
        case 'SET_TOYS':
            newState = { ...state, toys: [...action.toys] }
            break
        case 'REMOVE_TOY':
            newState = { ...state, toys: state.toys.filter(toy => toy._id !== action.toyId) }
            break
        case 'UPDATE_TOY':
            newState = {
                ...state, toys: state.toys.map(currToy => {
                    return (currToy._id === action.toy._id) ? action.toy : currToy
                })
            }
            break
        case 'ADD_TOY':
            newState = { ...state, toys: [...state.toys, action.toy] }
            break
        case 'SET_FILTER':
            newState = { ...state, filterBy: { ...action.filterBy } }
            break
        case 'SET_SORT':
            if (action.sortBy === 'name') {
                newState = { ...state, sortBy: { name: 1 } }
            } else if (action.sortBy === 'price') {
                newState = { ...state, sortBy: { price: 1 } }
            }
            if (action.sortBy === 'name' && state.sortBy && state.sortBy.name === 1) {
                newState = { ...state, sortBy: { name: -1 } }
            } else if (action.sortBy === 'price' && state.sortBy && state.sortBy.price === 1) {
                newState = { ...state, sortBy: { price: -1 } }
            }

        default:
            return newState;
    }

    return newState;
}




