
import React from "react";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie } from 'react-chartjs-2';

import { utilService } from '../../services/util.service'

// CMPS


ChartJS.register(ArcElement, Tooltip, Legend);



export function ToysPerYear({ toys }) {

    const yearMap = toys.reduce((map, toy) => {
        const year = new Date(toy.createdAt).getFullYear()
        if (!map[year]) map[year] = 0;
        map[year]++;
        return map
    }, {})




    const data = {
        labels: Object.keys(yearMap),
        datasets: [
            {
                label: '# of Votes',
                data: Object.values(yearMap),
                backgroundColor: Object.keys(yearMap).map(() => {
                    return utilService.getRandomColor()
                }),
                borderWidth: 1,
            },
        ],
    };


    return (
        <section className="toys-per-year">
            <h3>Toys Per Year</h3>
            <Pie data={data} />
        </section>
    )

}


