import React from "react";
import { connect } from "react-redux";

// ACTIONS
import { loadUsers, removeUser } from '../store/user.action'

// CMPS
import { User } from '../cmps/User'



class _ToyUser extends React.Component {
    state = {
    }

    componentDidMount = () => {
        this.props.loadUsers()

    }

    onRemoveUser = (userId) => {
        this.props.removeUser(userId)
    }

    render() {

        const { users, loggedinUser } = this.props
        if (!users) return <React.Fragment />

        return (
            <section className="toy-user">
                <h3>Users</h3>
                <table>
                    <thead>
                        <tr>
                            <th>Username</th>
                            <th>Full Name</th>
                            <th>Created At</th>
                            <th>Is Admin</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {users.map(user => {
                            return <User key={user._id} user={user} onRemoveUser={this.onRemoveUser} loggedinUser={loggedinUser} ></User>
                        })}
                    </tbody>
                </table>
            </section>
        )
    }
}



function mapStateToProps({ userModule }) {
    return {
        loggedinUser: userModule.loggedinUser,
        users: userModule.users
    }
}

const mapDispatchToProps = {
    loadUsers,
    removeUser
}

export const ToyUser = connect(mapStateToProps, mapDispatchToProps)(_ToyUser)